.project-heading-title {
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container-wrapper {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 3rem;

    .project-card {
        background: #1a1919;
        padding: 1.2rem 1rem;

        img {
            width: 100%;
        }

        .project-title {
            color: #fff;
            padding: 1rem 0;
        }

        .project-description {
            padding-bottom: 1rem;
            font-size: 1.1rem;
            text-align: justify;
        }

        .project-buttons {
            display: flex;
            justify-content: space-between;
            padding: 0.5rem 0;

            .btn {
                padding: 0.5rem 1rem;
            }
        }
    }
}


@media screen and (max-width: 740px) {
    .project-container-wrapper {
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}