.cards-wrapper {
    width: 100%;
    padding: 6rem 1rem;
    background: #000;

    .cards-content-wrapper {
        max-width: 1140px;
        margin: auto;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 40px;

        .card {
            border: 1px solid #eee;
            color: #eee;
            text-align: center;
            padding: 1rem;
            
            &:hover {
                background-color: #141414;
                transition: 0.5s;
            }

            h3 {
                font-size: 1.4rem;
                padding: 1rem;
            }

            p {
                padding: 10px 0px;
            }

            .card-line {
                border-bottom: 1px solid #eee;
                width: 10%;
                margin: 1.4rem auto;
                display: block;
            }

            .card-currency-title {
                font-size: 3rem;
                font-weight: 600;
                padding: 16px 0;
            }

            .btn {
                display: block;
                width: 80%;
                margin: 2rem auto;
            }
        }
    }
}

@media screen and (max-width: 740px) {
    .cards-content-wrapper {
    max-width: 90%;
    margin: auto;
    grid-template-columns: 1fr;
}
}